import React from 'react';
import './style.scss'

const Benefits = () => {
    return (
        <div className="app__benefits _container">
            <h2 className="title">Почему мы</h2>
            <div className="benefits__list">
                <div className="list__item">
                    <img src={require('../../assets/benefits/brand-image.png')} alt="" />
                    <p>Официальный фирменный магазин от бренда</p>
                </div>
                <div className="list__item">
                    <img src={require('../../assets/benefits/original.png')} alt="" />
                    <p>Оригинальный товар</p>
                </div>
                <div className="list__item">
                    <img src={require('../../assets/benefits/fast-delivery.png')} alt="" />
                    <p>Быстрая и надежная доставка</p>
                </div>
                <div className="list__item">
                    <img src={require('../../assets/benefits/track.png')} alt="" />
                    <p>Отправляем код отслеживания, фото заказа</p>
                </div>
                <div className="list__item">
                    <img src={require('../../assets/benefits/consulting.png')} alt="" />
                    <p>Профессиональная консультация и подбор</p>
                </div>
                <div className="list__item">
                    <img src={require('../../assets/benefits/best-price.png')} alt="" />
                    <p>Выгодные цены</p>
                </div>
            </div>
        </div>
    );
};

export default Benefits;